.first-section {
  position: relative;
  background: url("./web-temp-1-bg@2x.jpg") no-repeat center/cover;
  -webkit-transition: all 0.45s ease-out;
  -moz-transition: all 0.45s ease-out;
  -ms-transition: all 0.45s ease-out;
  -o-transition: all 0.45s ease-out;
  transition: all 0.45s ease-out; }
  .first-section .first-section--txt {
    position: absolute;
    top: 5.57291vw;
    right: 6.0938vw;
    z-index: 2; }
    .first-section .first-section--txt .first-section--txt__title {
      -webkit-transition: all 0.25s ease-out;
      -moz-transition: all 0.25s ease-out;
      -ms-transition: all 0.25s ease-out;
      -o-transition: all 0.25s ease-out;
      transition: all 0.25s ease-out;
      width: 31.1979vw;
      height: auto;
      display: inline-block;
      background: url("./web-temp-1-smart-temp-title@2x.png") no-repeat right/contain; }
      .first-section .first-section--txt .first-section--txt__title h1 {
        font-size: 7.5521vw;
        opacity: 0;
        color: #fff;
        font-weight: bold;
        letter-spacing: 4px;
        line-height: 0.95;
        text-align: left;
        z-index: 2;
        overflow: hidden; }
    .first-section .first-section--txt p {
      margin-top: 47px;
      font-size: 1.6666vw;
      line-height: 1.44;
      text-align: right;
      color: #fff;
      text-shadow: 0 0 20px rgba(0, 0, 0, 0.2); }
  .first-section .first-section--temp {
    position: absolute;
    top: 16.6666vw;
    left: 52.5vw;
    width: 25.7813vw;
    height: 23.4896vw;
    min-width: 340px;
    min-height: 500px;
    background: url("./web-temp-1-smart-temp@2x.png") no-repeat center/contain;
    overflow: hidden;
    text-indent: -9999px;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    -ms-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out; }

